.headroom-wrapper{
  z-index: 1000;
}

header {
  display: flex;
  flex-direction: row-reverse;
  padding: 20px;
  padding-right: 10px;
  padding-left: 15px;
  background-color: black;
  width: calc(100vw - 25px);
  z-index: 10000;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

header ul {
  display: flex;
}

header li {
  margin-right: 10px;
  margin-top: 10px;
  line-height: 200%;
}

header div {
  flex: 1;
}

header img {
  margin-top: 8px;
  width: 93px;
}

a {
  text-decoration: none;
  color: white;
}

.underBar {
  width: 10px;
  height: 10px;
  position: absolute;
  background-color: #DDDDDD;
  top: 60px;
  right: 190px;
  transition: 1s ease;
  border-radius: 50%;
}

.to {
  right: 210px;
  width: 10px;
}

.todeveloper {
  right: 140px;
  width: 10px;
}

.tocommunity {
  right: 60px;
  width: 10px;
}
.tologin, .toaccount, .toemailverify {
  display: none;
}