.Section {
    overflow: hidden !important;
}
.articleDiv {
    transition: 500ms;
    height: 100vh;
    box-sizing: border-box;
    text-align: center;
    padding: 82px 20px 20px 20px;
    background-size: cover !important;
    background-position: 40%;
    z-index: -10;
}

.headroom-wrapper {
    position: fixed;
}

.articleDiv h1::first-letter {
    color: #FFB400;
}

.ringContainer {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 10px;
    top: 30vh;
}

@media screen and (max-height: 720px) {
    .articleDiv {
        padding-top: 6px
    }
    .articleDiv.pinned {
        padding-top: 140px
    }
}