@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
body {
  background-color: black;
  margin: 0;
  color: white;
  overflow-x: hidden;
}
.SectionContainer {
  z-index: -100;
}
.Navigation {
  left: 30px !important;
  right: unset !important;
  z-index: 10000;
}
.Navigation .Navigation-Anchor {
  background-color: unset !important;
  background-image: url('https://i.imgur.com/aw1QIoU.png') !important;
}
.te-preview {
  background-color: white
}
/*.Navigation .active*/
.Section {
    overflow: hidden !important;
}
.articleDiv {
    -webkit-transition: 500ms;
    transition: 500ms;
    height: 100vh;
    box-sizing: border-box;
    text-align: center;
    padding: 82px 20px 20px 20px;
    background-size: cover !important;
    background-position: 40%;
    z-index: -10;
}

.headroom-wrapper {
    position: fixed;
}

.articleDiv h1::first-letter {
    color: #FFB400;
}

.ringContainer {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 10px;
    top: 30vh;
}

@media screen and (max-height: 720px) {
    .articleDiv {
        padding-top: 6px
    }
    .articleDiv.pinned {
        padding-top: 140px
    }
}
.plain h1::first-letter {
    color: black;
}

.plain h1 {
    margin: 0;
    margin-right: 10px;
    font-size: 17px;
    display: inline-block;
}

.plain {
    color: black;
    max-width: 900px;
    background-color: #E7E7E7;
    margin: auto;
    text-align: left;
    padding: 3px;
}

.commu > article > div.articleCon {
    min-height: 150px;
}

.commu > article {
    background-color: white;
    margin: 6px;
    margin-top: 12px;
    padding: 10px;
}

.commu .imgCon img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    vertical-align: -20px;
}

.commu .imgCon {
    width: 40px;
    height: 40px;
    display: inline-block;
    text-align: center;
    padding-top: 10px;
    overflow: hidden;
}

.commu .contentCon {
    display: inline-block;
    width: calc(100% - 75px);
    vertical-align: text-top;
    margin-top: -20px;
    padding-left: 10px;
    box-sizing: border-box;
    padding-bottom: 20px;
}

.commu .headCon span {
    opacity: 0.7;
}

.commu .headCon {
    margin-bottom: 10px;
}

.commu .content {
    line-height: 1.4em;
}

.commu .content img {
    max-width: 100%;
}

.commu .commentIcons {
    margin-top: 70px;
}

.commu .commentIcons > * {
    color: #5c6872;
    font-size: 20px;
}

.send_email {
    background-color: white;
    border: 1px solid black;
    margin: 5px
}

.uploadCon {
    background-color: #40c057;
    text-align: right;
    width: 86px;
    padding: 10px;
    font-size: 20px;
    color: white;
    border-radius: 10px;
    margin: 10px 10px 50px auto;
}

.uploadCon i {
    margin-right: 10px;
}

#searchbar {
    display: inline-block;
    width: calc(100% - 110px);
    height: 12px;
    margin: 20px;
    margin-right: 0px;
    padding: 10px;
}
#searchicon {
    background-color: white;
    display: inline-block;
    width: 30px;
    margin-left: -5px;
    padding-left: 10px;
    height: 34px;
    vertical-align: middle;
    line-height: 30px;
    border: 1px solid rgba(0, 0, 0, 0.3);
}

.firebaseui-idp-button {
    padding: 20px !important;
}

.headerpadding {
    height: 120px;
}

.modal {
    margin-left: auto;
    margin-right: auto;
    padding: 50px;
    width: 360px;
    background-color: rgba(100, 100, 100, 0.7);
}
.invisible {
    display: none;
}

.visible {
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    box-sizing: border-box;
    z-index: 10000;
    background-color: rgba(0, 0, 0, 0.7);
}
.visible #innerBox {
    width: 1080px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
}

@media screen and (max-width: 576px) {
    .modal {
        background-color: rgba(255, 255, 255, 0.8);
        width: 100vw;
        box-sizing: border-box;
    }
}
